<template>
  <div class="main-component row">
    <PageHeader :title="'Horários - Cursos'">
      <BaseButton
        title="Filtros"
        :type="'icon'"
        :color="'gray'"
        @click="openAsideModal('filtros')"
      >
        <i class="fas fa-list-ul"></i>
      </BaseButton>

      <BaseButton
        title="Relátorio"
        :type="'icon'"
        :color="'gray'"
        @click="openAsideModal('relatorio')"
      >
        <i class="fas fa-file-alt"></i>
      </BaseButton>

      <BaseButton
        title="Ajuda"
        :type="'icon'"
        :color="'lightblue'"
        @click="openAsideModal('ajuda')"
      >
        <i class="fas fa-question"></i>
      </BaseButton>
    </PageHeader>

    <div class="row w-100 m-0" v-show="!tableIsLoading">
      <div v-show="semestre1IsActived" class="w-100">
        <h2
          v-show="CursosWithHorarios.length || EletivasIsSelected"
          class="semestre-title"
        >
          1º SEMESTRE
        </h2>

        <template v-for="curso in CursosWithHorarios">
          <h3 class="curso-title pl-1" :key="1 + curso.nome">
            {{ curso.nome }}
          </h3>
          <ListTableHorarios
            :key="1 + curso.codigo + curso.value"
            :listaDePeriodos="listaDePeriodos"
            :curso="{
              nome: curso.nome,
              turno: curso.turno,
              horarios: curso.horarios1Semestre,
            }"
          />
        </template>

        <h3 v-show="EletivasIsSelected" class="curso-title pl-1">Eletivas</h3>
        <TableEletivas
          v-show="EletivasIsSelected"
          :Eletivas="horariosAtivos1.Eletivas"
        />
      </div>

      <div v-show="semestre2IsActived" class="w-100">
        <h2
          v-show="CursosWithHorarios.length || EletivasIsSelected"
          class="semestre-title"
        >
          2º SEMESTRE
        </h2>
        <template v-for="curso in CursosWithHorarios">
          <h3 class="curso-title pl-1" :key="2 + curso.nome">
            {{ curso.nome }}
          </h3>
          <ListTableHorarios
            :key="2 + curso.codigo + curso.value"
            :listaDePeriodos="listaDePeriodos"
            :curso="{
              nome: curso.nome,
              turno: curso.turno,
              horarios: curso.horarios2Semestre,
            }"
          />
        </template>

        <h3 v-show="EletivasIsSelected" class="curso-title pl-1">Eletivas</h3>
        <TableEletivas
          v-show="EletivasIsSelected"
          :Eletivas="horariosAtivos2.Eletivas"
        />
      </div>
    </div>

    <ModalFiltros
      ref="modalFiltros"
      :callbacks="modalFiltrosCallbacks"
      :tabsOptions="modalFiltrosTabs"
    >
      <div class="div-table">
        <BaseTable
          v-show="modalFiltrosTabs.current === 'Cursos'"
          :type="'modal'"
        >
          <template #thead>
            <th style="width: 25px"></th>
            <th
              class="clickable t-start"
              style="width: 50px"
              @click="toggleOrder(ordemCursos, 'codigo')"
            >
              Cód.
              <i :class="setIconByOrder(ordemCursos, 'codigo')"></i>
            </th>
            <th
              class="clickable t-start"
              style="width: 375px;"
              @click="toggleOrder(ordemCursos, 'nome')"
            >
              Nome
              <i :class="setIconByOrder(ordemCursos, 'nome')"></i>
            </th>
          </template>
          <template #tbody>
            <tr
              v-for="curso in CursosModalOrdered"
              :key="'curso-id-' + curso.codigo"
              @click="toggleItemInArray(curso, filtroCursos.selecionados)"
            >
              <td style="width: 25px">
                <input
                  type="checkbox"
                  :value="curso"
                  v-model="filtroCursos.selecionados"
                  class="form-check-input position-static m-0"
                />
              </td>
              <td style="width: 50px">
                {{ curso.codigo }}
              </td>
              <td style="width: 375px" class="t-start">
                {{ curso.nome }}
              </td>
            </tr>
          </template>
        </BaseTable>

        <BaseTable
          v-show="modalFiltrosTabs.current === 'Semestres'"
          :type="'modal'"
        >
          <template #thead>
            <th style="width: 25px"></th>
            <th style="width: 425px" class="t-start">
              Semestre Letivo
            </th>
          </template>
          <template #tbody>
            <tr @click="filtroSemestres.primeiro = !filtroSemestres.primeiro">
              <td style="width: 25px">
                <input
                  type="checkbox"
                  class="form-check-input position-static m-0"
                  v-model="filtroSemestres.primeiro"
                />
              </td>
              <td style="width: 425px" class="t-start">
                PRIMEIRO
              </td>
            </tr>
            <tr @click="filtroSemestres.segundo = !filtroSemestres.segundo">
              <td style="width: 25px">
                <input
                  type="checkbox"
                  class="form-check-input position-static m-0"
                  v-model="filtroSemestres.segundo"
                />
              </td>
              <td style="width: 425px" class="t-start">
                SEGUNDO
              </td>
            </tr>
          </template>
        </BaseTable>
      </div>
    </ModalFiltros>

    <ModalRelatorio ref="modalRelatorio" @selection-option="pdf($event)" />

    <ModalAjuda ref="modalAjuda">
      <li class="list-group-item">
        <b>Para exibir as tabelas de horários:</b> Clique no ícone filtros
        <i class="fas fa-list-ul icon-gray"></i> no cabeçalho da página e na
        janela que será aberta utilize as abas para navegar entre os tipos de
        filtros. Marque em suas respectivas tabelas quais informações deseja
        visualizar, e para finalizar clique no botão OK.
      </li>
      <li class="list-group-item">
        <b>Para gerar relatório dos horários:</b> Clique no ícone relatório
        <i class="fas fa-file-alt icon-gray"></i>, selecione se deseja gerar o
        relatório completo com todos os horários, ou apenas o relatório parcial
        com os horários que estão selecionados no momento.
      </li>
    </ModalAjuda>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import {
  toggleItemInArray,
  toggleOrdination,
  tableLoading,
} from "@/common/mixins";
import { PageHeader } from "@/components/ui";
import { ModalRelatorio, ModalAjuda, ModalFiltros } from "@/components/modals";

import TableEletivas from "./TableEletivas.vue";
import ListTableHorarios from "./ListTableHorarios.vue";

const allCursosOptions = [
  {
    nome: "SISTEMAS DE INFORMAÇÃO",
    codigo: "76A",
  },
  {
    nome: "CIÊNCIA DA COMPUTAÇÃO NOTURNO",
    codigo: "35A",
  },
  {
    nome: "CIÊNCIA DA COMPUTAÇÃO DIURNO",
    codigo: "65C",
  },
  {
    nome: "ENGENHARIA COMPUTACIONAL",
    codigo: "65B",
  },
  {
    nome: "ELETIVAS",
    codigo: "-",
  },
];

export default {
  name: "DashboardHorarios",
  mixins: [toggleItemInArray, toggleOrdination, tableLoading],
  components: {
    ModalFiltros,
    ModalAjuda,
    ModalRelatorio,
    ListTableHorarios,
    TableEletivas,
    PageHeader,
  },
  data() {
    return {
      planoAtual: null,
      ordemCursos: { order: "codigo", type: "asc" },
      listaDePeriodos: [],
      filtroCursos: {
        selecionados: [],
        ativados: [],
      },
      filtroSemestres: {
        primeiro: true,
        segundo: true,
        ativo: 3,
      },
      horariosAtivos1: {
        CCD: [[], [], [], [], [], [], [], [], [], []],
        CCN: [[], [], [], [], [], [], [], [], [], []],
        EC: [[], [], [], [], [], [], [], [], [], []],
        SI: [[], [], [], [], [], [], [], [], [], []],
        Eletivas: [],
      },
      horariosAtivos2: {
        CCD: [[], [], [], [], [], [], [], [], [], []],
        CCN: [[], [], [], [], [], [], [], [], [], []],
        EC: [[], [], [], [], [], [], [], [], [], []],
        SI: [[], [], [], [], [], [], [], [], [], []],
        Eletivas: [],
      },
      modalFiltrosTabs: {
        current: "Cursos",
        array: ["Cursos", "Semestres"],
      },
      modalFiltrosCallbacks: {
        selectAll: {
          Cursos: () => {
            this.filtroCursos.selecionados = [...allCursosOptions];
          },
          Semestres: () => {
            this.filtroSemestres.primeiro = true;
            this.filtroSemestres.segundo = true;
          },
        },
        selectNone: {
          Cursos: () => {
            this.filtroCursos.selecionados = [];
          },
          Semestres: () => {
            this.filtroSemestres.primeiro = false;
            this.filtroSemestres.segundo = false;
          },
        },
        btnOk: () => {
          this.setTableLoadingState(true);
          this.setSemestreAtivo();
          this.filtroCursos.ativados = [...this.filtroCursos.selecionados];
          this.setTableLoadingState(false);
        },
      },
      evenCCN: "false",
      evenCCD: "false",
      evenEC: "false",
      evenSI: "false",
    };
  },

  mounted() {
    this.currentPlano = this.$_.find(
      this.allPlanos,
      (plano) => plano.id == parseInt(localStorage.getItem("Plano"))
    );

    this.currentPlano = this.currentPlano;
    this.createHorarios1();
    this.createHorarios2();
    this.createListaDePeriodos();

    this.modalFiltrosCallbacks.selectAll.Cursos();
    this.filtroCursos.ativados = [...this.filtroCursos.selecionados];
  },

  methods: {
    openAsideModal(modalName) {
      if (modalName === "filtros") {
        this.$refs.modalFiltros.toggle();
        this.$refs.modalAjuda.close();
        this.$refs.modalRelatorio.close();
      } else if (modalName === "ajuda") {
        this.$refs.modalAjuda.toggle();
        this.$refs.modalFiltros.close();
        this.$refs.modalRelatorio.close();
      } else if (modalName === "relatorio") {
        this.$refs.modalRelatorio.toggle();
        this.$refs.modalAjuda.close();
        this.$refs.modalFiltros.close();
      }
    },
    setSemestreAtivo() {
      const { primeiro, segundo } = this.filtroSemestres;

      if (primeiro && !segundo) this.filtroSemestres.ativo = 1;
      else if (!primeiro && segundo) this.filtroSemestres.ativo = 2;
      else if (primeiro && segundo) this.filtroSemestres.ativo = 3;
      else this.filtroSemestres.ativo = undefined;
    },
    isEven(number) {
      if (number % 2 === 0) return "true";
      else return "false";
    },

    checkTurmaHorario(turma, horario) {
      if (turma.Horario1 == horario || turma.Horario2 == horario) {
        return true;
      } else return false;
    },
    checkPeriodo(turma, periodo) {
      for (
        var g = 0;
        g < this.$store.state.disciplinaGrade.DisciplinaGrades.length;
        g++
      ) {
        if (
          this.$store.state.disciplinaGrade.DisciplinaGrades[g].Disciplina ==
            turma &&
          this.$store.state.disciplinaGrade.DisciplinaGrades[g].periodo ==
            periodo
        ) {
          return true;
        }
      }
    },
    updateHorarios() {
      for (let i = 0; i < 10; i++) {
        for (let j = 0; j < this.horariosAtivos1.CCD[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos1.CCD[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos1.CCD[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
        for (let j = 0; j < this.horariosAtivos1.CCN[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos1.CCN[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos1.CCN[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
        for (let j = 0; j < this.horariosAtivos1.EC[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos1.EC[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos1.EC[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
        for (let j = 0; j < this.horariosAtivos1.SI[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos1.SI[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos1.SI[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
      }

      this.$store.commit("redefinirAtivas1", {
        Ativas: this.horariosAtivos1,
      });

      for (let i = 0; i < 10; i++) {
        for (let j = 0; j < this.horariosAtivos2.CCD[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos2.CCD[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos2.CCD[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
        for (let j = 0; j < this.horariosAtivos2.CCN[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos2.CCN[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos2.CCN[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
        for (let j = 0; j < this.horariosAtivos2.EC[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos2.EC[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos2.EC[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
        for (let j = 0; j < this.horariosAtivos2.SI[i].length; j++)
          for (let k = 0; k < this.TurmasInDisciplinasPerfis.length; k++) {
            if (
              this.horariosAtivos2.SI[i][j].id ==
              this.TurmasInDisciplinasPerfis[k].id
            ) {
              this.horariosAtivos2.SI[i].splice(
                j,
                1,
                this.TurmasInDisciplinasPerfis[k]
              );
            }
          }
      }

      this.$store.commit("redefinirAtivas1", {
        Ativas2: this.horariosAtivos2,
      });

      this.$store.commit("redefinirAtivas2", {
        Ativas: this.horariosAtivos2,
      });
    },
    createListaDePeriodos() {
      for (let i = 0; i < 10; i++) {
        this.listaDePeriodos.push({
          indice: i,
          nome: `${i + 1}º Período`,
        });
      }
    },
    createHorarios1() {
      var grade;
      var grades;
      var inicio = 1;
      var fim;
      var pedidos;
      var pedidosExternos;
      var disciplinaGrades = this.$store.state.disciplinaGrade.DisciplinaGrades;
      var turmas = this.$_.filter(this.TurmasInDisciplinasPerfis, [
        "periodo",
        1,
      ]);
      var turmasExternas = this.TurmasExternasInDisciplinas;

      const anoAtual = this.currentPlano.ano;
      var currentSemestres = 1;

      if (this.$store.state.curso.Cursos[0].semestreInicial == 1) {
        this.evenCCN = "false";
      } else if (this.$store.state.curso.Cursos[0].semestreInicial == 2) {
        this.evenCCN = "true";
      }

      if (this.$store.state.curso.Cursos[1].semestreInicial == 1) {
        this.evenEC = "false";
      } else if (this.$store.state.curso.Cursos[1].semestreInicial == 2) {
        this.evenEC = "true";
      }

      if (this.$store.state.curso.Cursos[2].semestreInicial == 1) {
        this.evenSI = "false";
      } else if (this.$store.state.curso.Cursos[2].semestreInicial == 2) {
        this.evenSI = "true";
      }

      if (this.$store.state.curso.Cursos[3].semestreInicial == 1) {
        this.evenCCD = "false";
      } else if (this.$store.state.curso.Cursos[3].semestreInicial == 2) {
        this.evenCCD = "true";
      }

      //CCD
      grades = this.$_.filter(this.$store.state.grade.Grades, ["Curso", 4]);
      grades = this.$_.orderBy(grades, "periodoInicio", "desc");
      pedidos = [];
      for (let t in this.$store.state.pedido.Pedidos) {
        for (let pedido in this.$store.state.pedido.Pedidos[t]) {
          if (this.$store.state.pedido.Pedidos[t][pedido].Curso === 4) {
            pedidos.push(this.$store.state.pedido.Pedidos[t][pedido]);
          }
        }
      }
      pedidosExternos = [];
      for (let t in this.$store.state.pedidoExterno.Pedidos) {
        for (let pedido in this.$store.state.pedidoExterno.Pedidos[t]) {
          if (this.$store.state.pedidoExterno.Pedidos[t][pedido].Curso === 4) {
            pedidosExternos.push(
              this.$store.state.pedidoExterno.Pedidos[t][pedido]
            );
          }
        }
      }

      for (let i = 0; i < grades.length && inicio <= 10; i++) {
        //grade
        grade = grades[i].id;
        //inicio
        if (i === 0) inicio = 1;
        else inicio = fim + 1;
        //fim
        if (i + 1 === grades.length) fim = 10;
        else if (i == 0)
          fim =
            1 +
            2 *
              (parseInt(anoAtual, 10) -
                parseInt(grades[i].periodoInicio.slice(0, 4), 10)) +
            (parseInt(currentSemestres, 10) -
              parseInt(grades[i].periodoInicio.slice(5, 6), 10)) /
              2;
        else
          fim =
            inicio -
            1 +
            2 *
              (parseInt(grades[i - 1].periodoInicio.slice(0, 4), 10) -
                parseInt(grades[i].periodoInicio.slice(0, 4), 10)) +
            (parseInt(grades[i - 1].periodoInicio.slice(5, 6), 10) -
              parseInt(grades[i].periodoInicio.slice(5, 6), 10)) /
              2;
        for (let k = 0; k < disciplinaGrades.length; k++) {
          if (
            disciplinaGrades[k].Grade == grade &&
            this.isEven(disciplinaGrades[k].periodo) == this.evenCCD &&
            disciplinaGrades[k].periodo >= parseInt(inicio, 10) &&
            disciplinaGrades[k].periodo <= parseInt(fim, 10)
          ) {
            for (let j = 0; j < turmas.length; j++) {
              if (turmas[j].Disciplina == disciplinaGrades[k].Disciplina) {
                for (let p = 0; p < pedidos.length; p++) {
                  if (
                    pedidos[p].vagasPeriodizadas > 0 &&
                    pedidos[p].Turma == turmas[j].id
                  ) {
                    this.horariosAtivos1.CCD[
                      disciplinaGrades[k].periodo - 1
                    ].push(turmas[j]);
                  }
                }
              }
            }
            for (let j = 0; j < turmasExternas.length; j++) {
              if (
                turmasExternas[j].periodo == 1 &&
                turmasExternas[j].Disciplina == disciplinaGrades[k].Disciplina
              ) {
                for (let p = 0; p < pedidosExternos.length; p++) {
                  if (
                    pedidosExternos[p].vagasPeriodizadas > 0 &&
                    pedidosExternos[p].Turma == turmasExternas[j].id
                  ) {
                    this.horariosAtivos1.CCD[
                      disciplinaGrades[k].periodo - 1
                    ].push(turmasExternas[j]);
                  }
                }
              }
            }
          }
        }
      }

      //CCN
      grades = this.$_.filter(this.$store.state.grade.Grades, ["Curso", 1]);
      grades = this.$_.orderBy(grades, "periodoInicio", "desc");
      pedidos = [];
      for (let t in this.$store.state.pedido.Pedidos) {
        for (let pedido in this.$store.state.pedido.Pedidos[t]) {
          if (this.$store.state.pedido.Pedidos[t][pedido].Curso === 1) {
            pedidos.push(this.$store.state.pedido.Pedidos[t][pedido]);
          }
        }
      }
      pedidosExternos = [];
      for (let t in this.$store.state.pedidoExterno.Pedidos) {
        for (let pedido in this.$store.state.pedidoExterno.Pedidos[t]) {
          if (this.$store.state.pedidoExterno.Pedidos[t][pedido].Curso === 1) {
            pedidosExternos.push(
              this.$store.state.pedidoExterno.Pedidos[t][pedido]
            );
          }
        }
      }
      for (let i = 0; i < grades.length && inicio <= 10; i++) {
        //grade
        grade = grades[i].id;
        //inicio
        if (i === 0) inicio = 1;
        else inicio = fim + 1;
        //fim
        if (i + 1 === grades.length) fim = 10;
        else if (i == 0)
          fim =
            1 +
            2 *
              (parseInt(anoAtual, 10) -
                parseInt(grades[i].periodoInicio.slice(0, 4), 10)) +
            (parseInt(currentSemestres, 10) -
              parseInt(grades[i].periodoInicio.slice(5, 6), 10)) /
              2;
        else
          fim =
            inicio -
            1 +
            2 *
              (parseInt(grades[i - 1].periodoInicio.slice(0, 4), 10) -
                parseInt(grades[i].periodoInicio.slice(0, 4), 10)) +
            (parseInt(grades[i - 1].periodoInicio.slice(5, 6), 10) -
              parseInt(grades[i].periodoInicio.slice(5, 6), 10)) /
              2;
        for (let k = 0; k < disciplinaGrades.length; k++) {
          if (
            disciplinaGrades[k].Grade == grade &&
            this.isEven(disciplinaGrades[k].periodo) == this.evenCCN &&
            disciplinaGrades[k].periodo >= parseInt(inicio, 10) &&
            disciplinaGrades[k].periodo <= parseInt(fim, 10)
          ) {
            for (let j = 0; j < turmas.length; j++) {
              if (turmas[j].Disciplina == disciplinaGrades[k].Disciplina) {
                for (let p = 0; p < pedidos.length; p++) {
                  if (
                    pedidos[p].vagasPeriodizadas > 0 &&
                    pedidos[p].Turma == turmas[j].id
                  ) {
                    this.horariosAtivos1.CCN[
                      disciplinaGrades[k].periodo - 1
                    ].push(turmas[j]);
                  }
                }
